.app-shell {
  background: #eae6df;
  min-height: 100vh;
  color: #111;
  font-family: 'SF Pro Display', 'Segoe UI', 'Helvetica Neue', sans-serif;
}

.brand {
  font-weight: 800;
  letter-spacing: -0.02em;
  color: #075e54;
}

.subtitle {
  margin: 4px 0 16px;
  color: #5f6368;
}

.topbar {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 12px 20px;
  background: linear-gradient(135deg, #128c7e, #075e54);
  color: #fff;
  font-weight: 700;
}

.actions {
  display: flex;
  gap: 8px;
}

.ghost-btn {
  background: transparent;
  border: 1px solid #c9d5d0;
  color: #128c7e;
  padding: 8px 12px;
  border-radius: 10px;
  cursor: pointer;
  transition: all 0.2s ease;
}

.ghost-btn:hover {
  border-color: #128c7e;
  background: rgba(18, 140, 126, 0.08);
}

.topbar .ghost-btn {
  border-color: rgba(255, 255, 255, 0.3);
  color: #fff;
}

.topbar .ghost-btn:hover {
  border-color: #fff;
  background: rgba(255, 255, 255, 0.08);
}

.primary-btn {
  background: #128c7e;
  color: #fff;
  border: none;
  padding: 10px 16px;
  border-radius: 12px;
  font-weight: 700;
  cursor: pointer;
  transition: transform 0.1s ease, box-shadow 0.1s ease;
  box-shadow: 0 4px 14px rgba(18, 140, 126, 0.25);
}

.primary-btn:hover {
  transform: translateY(-1px);
  box-shadow: 0 6px 18px rgba(18, 140, 126, 0.28);
}

.primary-btn.wide {
  width: 100%;
}

.layout {
  display: grid;
  grid-template-columns: 320px 1fr;
  height: calc(100vh - 64px);
}

.sidebar {
  background: #f7f8fa;
  border-right: 1px solid #d1d7db;
  display: flex;
  flex-direction: column;
}

.sidebar-header {
  padding: 14px 16px;
  font-weight: 700;
  color: #075e54;
  border-bottom: 1px solid #e2e5e9;
}

.contact {
  display: flex;
  gap: 12px;
  align-items: center;
  padding: 12px 16px;
  border: none;
  background: transparent;
  width: 100%;
  text-align: left;
  cursor: pointer;
  transition: background 0.15s ease;
}

.contact:hover {
  background: #e9edef;
}

.contact.active {
  background: #d7f8ef;
}

.contact.self {
  opacity: 0.7;
  cursor: not-allowed;
}

.contact-info {
  display: flex;
  flex-direction: column;
  gap: 4px;
}

.contact-name {
  font-weight: 800;
}

.contact-status {
  color: #5f6368;
  font-size: 13px;
}

.avatar {
  width: 44px;
  height: 44px;
  border-radius: 50%;
  object-fit: cover;
  border: 2px solid #fff;
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.12);
}

.chat-area {
  background: url('https://images.unsplash.com/photo-1444703686981-a3abbc4d4fe3?auto=format&fit=crop&w=1600&q=80')
    center/cover no-repeat fixed;
  position: relative;
  display: flex;
  flex-direction: column;
}

.chat-header {
  padding: 12px 16px;
  border-bottom: 1px solid #d1d7db;
  display: flex;
  align-items: center;
  justify-content: space-between;
  background: #f0f2f5;
}

.chat-peer {
  display: flex;
  gap: 10px;
  align-items: center;
}

.chat-name {
  font-weight: 800;
}

.chat-status {
  color: #4e555c;
  font-size: 13px;
}

.message-list {
  flex: 1;
  overflow-y: auto;
  padding: 18px 18px 110px;
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.message-row {
  display: flex;
}

.message-row.from-self {
  justify-content: flex-end;
}

.message-row.from-them {
  justify-content: flex-start;
}

.message-bubble {
  background: #dcf8c6;
  padding: 10px 12px;
  border-radius: 12px;
  max-width: 60%;
  box-shadow: 0 3px 10px rgba(0, 0, 0, 0.08);
}

.message-row.from-them .message-bubble {
  background: #fff;
}

.message-text {
  line-height: 1.5;
}

.composer {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  padding: 12px 16px;
  display: flex;
  gap: 8px;
  align-items: center;
  background: #f0f2f5;
  border-top: 1px solid #d1d7db;
}

.message-input {
  flex: 1;
  padding: 12px 14px;
  border: 1px solid #d1d7db;
  border-radius: 14px;
  font-size: 15px;
  background: #fff;
}

.send-btn {
  min-width: 80px;
}

.emoji-btn {
  border: none;
  background: #fff;
  width: 42px;
  height: 42px;
  border-radius: 14px;
  font-size: 20px;
  cursor: pointer;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.08);
}

.emoji-btn.active {
  background: #d7f8ef;
}

.emoji-panel {
  position: absolute;
  bottom: 62px;
  left: 14px;
  background: #fff;
  padding: 10px;
  border-radius: 12px;
  box-shadow: 0 12px 28px rgba(0, 0, 0, 0.12);
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  gap: 8px;
  z-index: 2;
}

.emoji-panel button {
  border: none;
  background: transparent;
  font-size: 20px;
  cursor: pointer;
}

.empty-state {
  margin: auto;
  color: #5f6368;
  font-size: 14px;
}

.auth-screen {
  min-height: 100vh;
  display: grid;
  place-items: center;
  background: linear-gradient(135deg, #f0eadc, #e3d8c6);
}

.auth-shell {
  width: 100%;
  padding: 40px 16px;
}

.auth-card {
  background: #fffdf8;
  padding: 24px;
  border-radius: 24px;
  box-shadow: 0 18px 50px rgba(0, 0, 0, 0.12);
}

.login-layout {
  max-width: 1100px;
  margin: 0 auto;
  display: grid;
  gap: 20px;
  grid-template-columns: 1.2fr 1fr;
}

.login-left {
  background: #f6efe2;
  border: 1px solid #e6dfcf;
  border-radius: 16px;
  padding: 22px;
}

.brand-row {
  display: flex;
  align-items: center;
  gap: 10px;
  font-weight: 800;
  color: #128c7e;
}

.brand-icon {
  font-size: 22px;
}

.brand-text {
  font-size: 18px;
}

.login-title {
  margin: 12px 0 8px;
  font-size: 26px;
}

.steps {
  padding-left: 18px;
  color: #4e555c;
  line-height: 1.6;
}

.quick-actions {
  display: flex;
  flex-wrap: wrap;
  gap: 8px;
  margin-top: 14px;
}

.login-right {
  display: flex;
  flex-direction: column;
  gap: 16px;
}

.qr-card {
  border: 1px solid #d1d7db;
  border-radius: 18px;
  overflow: hidden;
  background: #fff;
  display: grid;
  grid-template-rows: 100px 1fr;
}

.qr-placeholder {
  display: grid;
  place-items: center;
  background: #e8f7f3;
  color: #128c7e;
  font-weight: 800;
  letter-spacing: 0.08em;
}

.qr-body {
  padding: 18px;
  display: flex;
  flex-direction: column;
  gap: 14px;
}

.login-form {
  display: flex;
  flex-direction: column;
  gap: 12px;
}

.field {
  display: flex;
  flex-direction: column;
  gap: 6px;
}

.field span {
  font-weight: 700;
  color: #4e555c;
}

.field input {
  padding: 10px 12px;
  border: 1px solid #d1d7db;
  border-radius: 12px;
  font-size: 15px;
}

.error {
  background: #fdecea;
  color: #d93025;
  padding: 10px 12px;
  border-radius: 12px;
}

.quick-login {
  display: flex;
  gap: 8px;
  flex-wrap: wrap;
}

.copyright {
  text-align: center;
  color: #4e555c;
  font-weight: 700;
}

.modal-backdrop {
  position: fixed;
  inset: 0;
  background: rgba(0, 0, 0, 0.32);
  display: grid;
  place-items: center;
  z-index: 10;
  padding: 20px;
}

.modal {
  background: #fff;
  width: 520px;
  max-width: 100%;
  border-radius: 20px;
  padding: 20px;
  box-shadow: 0 20px 60px rgba(0, 0, 0, 0.18);
}

.modal-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 16px;
}

.modal-title {
  font-weight: 800;
}

.modal-subtitle {
  font-size: 13px;
  color: #5f6368;
}

.profile {
  display: flex;
  gap: 16px;
  align-items: center;
}

.profile-avatar {
  width: 96px;
  height: 96px;
  border-radius: 50%;
  object-fit: cover;
  box-shadow: 0 6px 16px rgba(0, 0, 0, 0.16);
}

.profile-fields {
  flex: 1;
  display: flex;
  flex-direction: column;
  gap: 12px;
}

.file-field input {
  padding: 8px 12px;
}

@media (max-width: 960px) {
  .layout {
    grid-template-columns: 1fr;
  }

  .sidebar {
    height: 260px;
  }

  .chat-area {
    height: calc(100vh - 64px - 260px);
  }
}
